@import url("https://fonts.googleapis.com/css?family=Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i&subset=cyrillic,cyrillic-ext,greek,greek-ext,latin-ext,vietnamese");
@import url("https://cdnjs.cloudflare.com/ajax/libs/MaterialDesign-Webfont/2.1.19/css/materialdesignicons.css");
body {
  margin: 0px;
  width: 100vw;
  height: 100vh;
  position: absolute;
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: #353a3a;
  color: white;
  overflow: hidden;
  font-family: "Roboto", sans-serif;
}

body *,
body *::before,
body *::after {
  box-sizing: border-box;
}

body > div[ui-view] {
  height: calc(100vh - 64px);
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5vw 15vw;
}

@media (max-width: 800px) {
  body > div[ui-view] {
    padding: 0px;
  }
}

body > div#start > div#shoutbox {
  width: 100%;
  height: 100%;
  background-color: white;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  padding: 5px;
}

body > div#start > div#shoutbox > div#chat {
  flex-grow: 1;
  margin-bottom: 5px;
  border-radius: 5px;
  background-color: #eaecec;
  overflow: auto;
