  border: none;
  padding: 1vw;
  color: #292d2d;
  background-color: #eaecec;
  border-radius: 5px;
  transition: all 0.4s;
  font-family: "Roboto", sans-serif;
}

body > div#start > div#shoutbox > div#footer > input.ng-touched.ng-empty {
  background-color: #ff8282;
}

