}

body > div#start > div#shoutbox > div#footer > label[for="image"] > input#image-upload, body > div#start > div#shoutbox > div#footer button[for="image"] > input#image-upload, body > div#start > div#shoutbox > div#footer div#image-options > span[for="image"] > input#image-upload {
